import React from "react";
import Scrollbars from "react-custom-scrollbars";
import { Select } from "@windmill/react-ui";

import Title from "../form/Title";
import Error from "../form/Error";
import LabelArea from "../form/LabelArea";
import InputArea from "../form/InputArea";
import DrawerButton from "../form/DrawerButton";
import Uploader from "../image-uploader/Uploader";
import ParentCategory from "../category/ParentCategory";
import ProductSelect from "../product/ProductSelect";
import useBannersSubmit from "../../hooks/useBannersSubmit";

const BannerDrawer = ({ id }) => {
  const {
    register,
    handleSubmit,
    onSubmit,
    errors,
    imageUrl,
    imageUrl2,
    setImageUrl,
    setImageUrl2,
  } = useBannersSubmit(id);

  return (
    <>
      <div className="w-full relative p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300">
        {id ? (
          <Title
            title="Update Banner"
            description="Updated your product's banner and necessary information from here"
          />
        ) : (
          <Title
            title="Add Banner"
            description="Add your product's banner and necessary information from here"
          />
        )}
      </div>
      <Scrollbars className="w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <form onSubmit={handleSubmit(onSubmit)} className="block">
          <div className="px-6 pt-8 flex-grow w-full h-full max-h-full pb-40 md:pb-32 lg:pb-32 xl:pb-32">
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Product Image 1" />
              <div className="col-span-8 sm:col-span-4">
                <Uploader imageUrl={imageUrl} setImageUrl={setImageUrl} />
              </div>
            </div>
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Product Image 2" />
              <div className="col-span-8 sm:col-span-4">
                <Uploader imageUrl={imageUrl2} setImageUrl={setImageUrl2} />
              </div>
            </div>

            {/* <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Products" />
              <div className="col-span-8 sm:col-span-4">
                <Select
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-white border-transparent focus:bg-white"
                  name="pid"
                  {...register("pid", {
                    required: "Product parent category is required!",
                  })}
                >
                  <option value="" defaultValue hidden>
                    Select Product
                  </option>
                  <ProductSelect showId={true} />
                </Select>
                <Error errorName={errors.pid} />
              </div>
            </div> */}

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Description 1" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  label="Description 1"
                  name="text1"
                  type="text"
                  placeholder="Description 1"
                />
                <Error errorName={errors.desc} />
              </div>
            </div>
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Description 2" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  label="Description 2"
                  name="text2"
                  type="text"
                  placeholder="Description 2"
                />
                <Error errorName={errors.desc} />
              </div>
            </div>
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Description 3" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  label="Description 3"
                  name="text3"
                  type="text"
                  placeholder="Description 3"
                />
                <Error errorName={errors.desc} />
              </div>
            </div>
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Description 4" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  label="Description 4"
                  name="text4"
                  type="text"
                  placeholder="Description 4"
                />
                {/* <Error errorName={errors.desc} /> */}
              </div>
            </div>
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Discount Percentage" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  label="Discount Percentage"
                  name="discountPercentage"
                  type="text"
                  placeholder="Discount percentage"
                />
                <Error errorName={errors.desc} />
              </div>
            </div>
            {/* <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Parent Category" />
              <div className="col-span-8 sm:col-span-4">
                <Select
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-white border-transparent focus:bg-white"
                  name="cid"
                  {...register("cid", {
                    required: "Category is required!",
                  })}
                >
                  <option value="" defaultValue hidden>
                    Select parent category
                  </option>
                  <ParentCategory showId={true} />
                </Select>
                <Error errorName={errors.cid} />
              </div>
            </div> */}
            {/* <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="In Section No" />
              <div className="col-span-8 sm:col-span-4">
                <Select
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-white border-transparent focus:bg-white"
                  name="section"
                  {...register("section", {
                    required: "Section is required!",
                  })}
                >
                  <option value="" hidden>
                    Select section
                  </option>
                  {[1, 2, 3, 4, 5, 6, 7, 8, 9, 0].map((ele, idx) => (
                    <option
                      key={idx * idx}
                      value={ele}
                      defaultValue={ele === 1}
                    >
                      Section no {ele}
                    </option>
                  ))}
                </Select>
                <Error errorName={errors.section} />
              </div>
            </div> */}
          </div>

          <DrawerButton id={id} title="Banners" />
        </form>
      </Scrollbars>
    </>
  );
};

export default React.memo(BannerDrawer);
