import React from 'react';

import useAsync from '../../hooks/useAsync';
import ProductServices from '../../services/ProductServices';


const ProductSelect = ({ showId = false }) => {
    const { data } = useAsync(ProductServices.getAllProducts); //   console.log(value);
    return (
        <>
            {showId ? data.length > 0 && data.map((parent) => (
                <option key={parent._id} value={parent._id}>
                    {parent.product_name}
                </option>
            )) : data.length > 0 && data.map((parent) => (
                <option key={parent._id} value={parent.product_name}>
                    {parent.product_name}
                </option>))}
        </>
    );
};

export default ProductSelect;