import React from "react";
// import { Link } from 'react-router-dom';
import {
  TableCell,
  TableBody,
  TableRow,
  // Badge,
  Avatar,
} from "@windmill/react-ui";
// import { FiEye } from 'react-icons/fi';
// import Tooltip from '../tooltip/Tooltip';
import MainModal from "../modal/MainModal";
import MainDrawer from "../drawer/MainDrawer";
import BannerDrawer from "../drawer/BannerDrawer";
import ShowHideButton from "../table/ShowHideButton";
import EditDeleteButton from "../table/EditDeleteButton";
import useToggleDrawer from "../../hooks/useToggleDrawer";

const ProductTable = ({ banners }) => {
  const { serviceId, handleModalOpen, handleUpdate } = useToggleDrawer();
  console.log(banners);
  return (
    <>
      <MainModal id={serviceId} />
      <MainDrawer>
        <BannerDrawer id={serviceId} />
      </MainDrawer>
      <TableBody>
        {banners?.map((banner, i) => (
          <TableRow key={i + 1}>
            <TableCell>
              <span className="text-xs uppercase font-semibold">
                {" "}
                {banner._id.substring(18, 26)}
              </span>
            </TableCell>
            <TableCell>
              <Avatar
                className="hidden p-1 mr-2 md:block bg-gray-50 shadow-none"
                src={banner.image}
                alt={banner?.products?.product_name}
              />
            </TableCell>
            <TableCell>
              <Avatar
                className="hidden p-1 mr-2 md:block bg-gray-50 shadow-none"
                src={banner.image}
                alt={banner?.products?.product_name}
              />
            </TableCell>
            <TableCell>
              <div className="flex items-center">
                <div>
                  <h2 className="text-sm font-medium">{banner.text1}</h2>
                </div>
              </div>
            </TableCell>
            <TableCell>
              <div className="flex items-center">
                <div>
                  <h2 className="text-sm font-medium">{banner.text2}</h2>
                </div>
              </div>
            </TableCell>
            <TableCell>
              <span className="text-sm">{banner.text3}</span>
            </TableCell>

            <TableCell>
              <span className="text-sm font-semibold">{banner.text4}</span>
            </TableCell>
            <TableCell>
              <span className="text-sm font-semibold">
                {banner.discountPercent}
              </span>
            </TableCell>

            {/* <TableCell>
              <Link
                to={`/banner/${banner._id}`}
                className="flex justify-center text-center text-gray-400 hover:text-green-600"
              >
                <Tooltip
                  id="details"
                  Icon={FiEye}
                  title="Details"
                  bgColor="#10B981"
                />
              </Link>
            </TableCell> */}

            <TableCell>
              <ShowHideButton id={banner._id} status={banner.published} />
            </TableCell>
            <TableCell>
              <EditDeleteButton
                id={banner._id}
                handleUpdate={handleUpdate}
                handleModalOpen={handleModalOpen}
              />
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default React.memo(ProductTable);
