import { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { SidebarContext } from "../context/SidebarContext";
import BannerServices from "../services/BannerServices";
import { notifyError, notifySuccess } from "../utils/toast";

const useBannerSubmit = (id) => {
  const [imageUrl, setImageUrl] = useState([]);
  const [imageUrl2, setImageUrl2] = useState([]);
  const { isDrawerOpen, closeDrawer, setIsUpdate } = useContext(SidebarContext);

  const {
    register,
    handleSubmit,
    watch,
    setValue,
    clearErrors,
    formState: { errors },
  } = useForm();

  const onSubmit = (data) => {
    // alert('add banner btn clicked')
    // if (imageUrl.length <= 0) {
    //   notifyError("Image is required!");
    //   return;
    // }

    const bannerData = {
      text1: data.text1,
      text2: data.text2,
      text3: data.text3,
      text4: data.text4,
      discountPercent: data.discountPercent,
      image1: imageUrl[0]?.path,
      image2: imageUrl2[0]?.path,
    };

    if (id) {
      BannerServices.updateBanner(id, bannerData)
        .then((res) => {
          setIsUpdate(true);
          notifySuccess(res.message);
        })
        .catch((err) => notifyError(err.message));
      closeDrawer();
    } else {
      BannerServices.addBanner(bannerData)
        .then((res) => {
          setIsUpdate(true);
          notifySuccess(res.message);
        })
        .catch((err) => notifyError(err.message));
      closeDrawer();
    }
  };

  useEffect(() => {
    if (!isDrawerOpen) {
      setValue("text1");
      setValue("text2");
      setValue("text3");
      setValue("text4");
      setValue("discountPercent");
      setImageUrl([]);
      setImageUrl2([]);
      clearErrors("text1");
      clearErrors("text2");
      clearErrors("image");
      clearErrors("text3");
      clearErrors("text4");
      return;
    }

    if (id) {
      BannerServices.getBannerById(id)
        .then((data) => {
          // console.log(data)
          if (data) {
            setValue("text1", data.text1);
            setValue("text2", data.text2);
            setValue("text3", data.text3);
            setValue("text4", data.text4);
            setValue("discountPercent", data.discountPercent);
            setImageUrl([{ path: data.imageUrl, name: data.imageUrl }]);
            setImageUrl2([{ path: data.imageUrl2, name: data.imageUrl2 }]);
          }
        })
        .catch((err) => {
          console.log(err);
          notifyError("There is a server error!");
        });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id, setValue, isDrawerOpen]);

  return {
    register,
    watch,
    handleSubmit,
    onSubmit,
    errors,
    imageUrl,
    setImageUrl,
  };
};

export default useBannerSubmit;
